body {
  margin: 0;
  padding: 0;
  font-family: Muli, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eee
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#conteudo {
    width: 100%;
    margin-top: 120px;
    position: absolute;
    margin-bottom: 50px;
}
.spinner {
    margin: 300px auto;
    width: 40px;
    height: 40px;
    position: relative;
    text-align: center;
    -webkit-animation: sk-rotate 2.0s infinite linear;
    animation: sk-rotate 2.0s infinite linear;
  }
  
  .dot1, .dot2 {
    width: 60%;
    height: 60%;
    display: inline-block;
    position: absolute;
    top: 0;
    background-color: #fece2a;
    border-radius: 100%;
    
    -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
    animation: sk-bounce 2.0s infinite ease-in-out;
  }
  
  .dot2 {
    top: auto;
    bottom: 0;
    background-color: #17023f;
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
  }
  
  @-webkit-keyframes sk-rotate { 100% { -webkit-transform: rotate(360deg) }}
  @keyframes sk-rotate { 100% { transform: rotate(360deg); -webkit-transform: rotate(360deg) }}
  
  @-webkit-keyframes sk-bounce {
    0%, 100% { -webkit-transform: scale(0.0) }
    50% { -webkit-transform: scale(1.0) }
  }
  
  @keyframes sk-bounce {
    0%, 100% { 
      transform: scale(0.0);
      -webkit-transform: scale(0.0);
    } 50% { 
      transform: scale(1.0);
      -webkit-transform: scale(1.0);
    }
  }
#noresults{
    margin: 0;
    position: absolute;
    top: 72px;
    width: 100%;
    height: 312px;
    background: transparent url(//b.thumbs.redditmedia.com/-wGyxkikH8fHaGpHBWKFHD_nQ_Ed-aZxkaZ6dh2yBJI.png) no-repeat 50% 50%;
    text-indent: -9999px;
    font-size: 0;
    display: block;
    -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#topo {
  width: 100%;
  height: 132px;
  z-index: 1;
  position: fixed;
  background-color: rgba(238,238,238, );
}

#tituloDaPagina {
  align-items: center;
  display: flex;
  width: 100%;
  height: 60px;
  background-color: #17023f;
  color: white;
  justify-content: center;
  font-size: 36px;
  box-shadow: 1px 3px 8px -1px #888888;    
  /* font-family: Muli, Arial, sans-serif; */
  
}

#tabs {
  display: flex;
  justify-content: center;
  margin-top: 1em;
}

.tabBtn {
    all: unset;
    border-radius: 6px;
    color: white;
    background-color: #029c96;
    width: 80px;
    height: 40px;
    margin: 0 1vmin 0 1vmin;
    text-align: center;
    box-shadow:  1px 2px 4px 0px #888888;
}

.tabBtn:hover {
  cursor: pointer;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  cursor: pointer;
  background-color: #fece2a;
  color: #17023f;
}

.active {
  background-color: #fece2a;
  color: #17023f
}
